import {useState, useEffect} from 'react';
import TableData from "./TableData";
import {ClassName} from "../Other/HtmlHandlers";
import "./TableExercise.css";

function TableHeader({headerData, id}) {
    return (
        <thead>
            <tr>
                {headerData.map((hItem, index) => <th key={id + '-h' + index} id={id + '-h' + index}>{hItem}</th>)}
            </tr>
        </thead>
    );
}

function TableRows({rowData, id, selectedCell, CellSelectedCallBack}) {
    return (
        <tbody>
            {rowData.map((rItem, index) => <tr key={id + '-r' + index} id={id + '-r' + index}><TableColumns columnData={rItem} id={id} selectedCell={selectedCell} CellSelectedCallBack={CellSelectedCallBack}/></tr>)}
        </tbody>
    );
}

function TableColumns({columnData, id, selectedCell, CellSelectedCallBack}) {
    /*
    const ClearPreviousCellSelections = () => {
        let cells = document.getElementById(id).getElementsByTagName('td');
        //console.log(cells);
        for (let i = 0; i < cells.length; i++) {
            cells[i].className = ClassName.Remove(cells[i].className, 'selected');
        }
    }
    
    const CellSelected = (event) => {
        // Select/Deselect table cell
        let classNameString = event.target.className;
        ClearPreviousCellSelections();
        event.target.className = ClassName.Add(classNameString, 'selected');
        CellSelectedCallBack(event.target.id);
        //console.log(event.target.id);
        
        //let testAttribute = 'a1 a2 b1 b2 c1 x1 x1 x1';
        //testAttribute = ClassName.Add(testAttribute, 'c2');
        //testAttribute = ClassName.Remove(testAttribute, 'a1');
        //testAttribute = ClassName.Remove(testAttribute, 'x1');
        //console.log(testAttribute);
    }
    */

    const tableCell = (cid, value) => {
        return <td key={id + '-c' + cid} id={id + '-c' + cid} className={selectedCell === cid ? 'selected' : ''} onClick={() => CellSelectedCallBack(cid)} >{value}</td>
    }


    //return columnData.map((cItem) => <td key={id + '-c' + cItem.id} id={id + '-c' + cItem.id} className='a1 b2 c3' onClick={() => CellSelectedCallBack(cItem.id)} >{cItem.value}</td>);
    return columnData.map((cItem) => tableCell(cItem.id, cItem.value));
}

function Table({tableData, id}) {
    const [headerData, setHeaderData] = useState(tableData.getHeaderData());
    const [rowData, setRowData] = useState(tableData.getRowData());
    const [selectedCell, setSelectedCell] = useState('00');
    
    const CellSelected = (id) => {
        console.log(id);
        setSelectedCell(id);
    }

    const GetSelectedCellValue = () => {
        if (selectedCell === '')
            return '';
        let cellValue = '';
        let cellIndexes = GetSelectedCellIndexes(rowData);
        cellValue = rowData[cellIndexes.rowIndex][cellIndexes.columnIndex].value;
        /*
        rowData.forEach(row => {
            row.forEach(cell => {
                if (cell.id === selectedCell)
                    cellValue = cell.value;
            });
        });
        */
        console.log(cellValue);
        return cellValue;
    }

    const GetSelectedCellIndexes = (rowData) => {
        let row;
        for (let r = 0; r < rowData.length; r++) {
            row = rowData[r];
            for (let c = 0; c < row.length; c++) {
                if (row[c].id === selectedCell)
                    return {rowIndex: r, columnIndex: c};
            }
        }
    }

    const UpdateCell = (value) => {
        let selectedCellIndexes = GetSelectedCellIndexes(rowData);
        let rowIndex = selectedCellIndexes.rowIndex;
        let columnIndex = selectedCellIndexes.columnIndex;
        
        setRowData(prevRowData => {
            return prevRowData.map((rItem, index) => {
                if (index !== rowIndex) return rItem;
                return rItem.map((cItem, index) => {
                    if (index !== columnIndex) return cItem;
                    return {
                      ...cItem,
                      value: value
                    };
                }) 
            })
        });
    }

    return (
        <>
        <table id={id}>
            <TableHeader headerData={headerData} id={id} />
            <TableRows rowData={rowData} id={id} selectedCell={selectedCell} CellSelectedCallBack={CellSelected} />
        </table>
        <TableEdit value={GetSelectedCellValue} UpdateCellCallBack={UpdateCell} />
        </>
    );
}

function TableEdit({value, UpdateCellCallBack}) {
    const [cellValue, setCellValue] = useState(value);

    useEffect(() => {
        setCellValue(value);
    }, [value]);
    
    const HandleCellEdit = (event) => {
        event.preventDefault();
        //console.log('Value Submitted: ' + cellValue);
        UpdateCellCallBack(cellValue);
    }

    return(
        <form onSubmit={HandleCellEdit}>
            <label>Edit Selected Cell
                <input
                    className='cell-edit-field' 
                    type='text'
                    value={cellValue}
                    onChange={(event) => setCellValue(event.target.value)}
                />
            </label> 
            <input value='Save' type='submit' className='save-input' />
        </form>
    );
}

